import { doc, getFirestore, updateDoc } from "firebase/firestore";
import { getStorage } from "firebase/storage";
import React, { useState } from "react";
import { Button } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import { getDownloadURL, ref, uploadBytes } from "firebase/storage";
import { ClipLoader } from "react-spinners";
import Swal from "sweetalert2";

const EditServices = () => {
    const location = useLocation();
    const service = location.state?.service;

    const [title, setTitle] = useState(service?.title || "");
    const [description, setDescription] = useState(service?.description || "");
    const [image, setImage] = useState(null);
    const [loading, setLoading] = useState(false);

    // Firebase Instances
    const storage = getStorage();
    const db = getFirestore();

    const handleImageChange = (event) => {
        //    target 
        if (event.target && event.target.files && event.target.files[0]) {
          const file = event.target.files[0]; //    
          setImage(file); //     useState
          console.log("  :", file.name);
        } else {
          console.error("    ");
        }
    };

    const handleUpdate = async (e) => {
        e.preventDefault();
        setLoading(true);

        try {
            let imageUrl = service.image || ""; //   

            // Step 1:    Firebase Storage (    )
            if (image) {
                const storageRef = ref(storage, `services/${image.name}`);
                await uploadBytes(storageRef, image); //  
                imageUrl = await getDownloadURL(storageRef); //     
            }

            // Step 2:     Firestore
            const servicesRef = doc(db, "services", service.id); //  
            await updateDoc(servicesRef, {
              title,
              description,
              imageUrl, //   
            });

            Swal.fire({
                icon: "success",
                title: " ",
                text: "   "
            });
        }
        catch(error){
            console.error("   :", error);
            alert("  !");
        }
        finally {
            setLoading(false);
        }
    }

    return (
        <>
        <div className="project_edit">
            <h3> </h3>
            {loading ? (
                <div className="loading" style={{ marginTop: "20px" }}>
                    <ClipLoader color="#007bff" size={50} />
                    <p> ...</p>
                </div>
            ) : (
                <form onSubmit={handleUpdate}>
                    <input 
                        className="form-control mb-2" 
                        type="text" 
                        placeholder=" " 
                        value={title} 
                        onChange={(e) => setTitle(e.target.value)} 
                    />

                    <textarea className="form-control mb-2" placeholder=" " cols="3" rows="3" value={description} onChange={(e) => setDescription(e.target.value)}></textarea>

                    <label>  </label>

                    <input
                        className="form-control mb-2"
                        type="file"
                        onChange={handleImageChange}
                        accept="image/*"
                    />

                    <Button type="submit" variant="success"> </Button>

                </form>
            )}
        </div>
        </>
    )
}

export default EditServices;