import React, { useEffect, useState } from "react";
import { collection, getDocs, deleteDoc, doc } from "firebase/firestore";
import { db } from '../../../firebase';
import { Button, Table } from "react-bootstrap";
import { NavLink } from 'react-router-dom'
import { ClipLoader } from "react-spinners";

const IndexProject = () => {
    const [projects, setProjects] = useState([]);
    const [refresh, setRefresh] = useState(false);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        fetchProjects();
    }, [refresh])

    const fetchProjects = async () => {
        setIsLoading(true); //  
        const querySnapshot = await getDocs(collection(db, 'projects'));
        const projectsData = querySnapshot.docs.map((doc) => ({
            id: doc.id,
            ...doc.data()
        }));
        setProjects(projectsData);
        setIsLoading(false);
    }

    //  
    const handleDelete = async (id) => {
        if (window.confirm("   ")) {
        await deleteDoc(doc(db, "projects", id));
        setRefresh((prev) => !prev);
        }
    };

    return (
        <>
            <div className="myprojects">
                {isLoading ? (
                    <div className="loading" style={{ marginTop: "20px" }}>
                        <ClipLoader color="#007bff" size={50} />
                        <p> ...</p>
                    </div>   
                ) : (
                    <div className="project_container">

                        <div className="project_manager_header">
                            <h3> </h3>
                            <Button variant="success">
                                <NavLink to="/control/project/add" className="text-white">
                                      
                                </NavLink>
                            </Button>
                        </div>
                        <div className="projectView">
                            <Table striped bordered hover>
                                <thead>
                                    <tr>
                                        <th> </th>
                                        <th> </th>
                                        <th> </th>
                                        <th> </th>
                                        <th> </th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        projects.map((p) => (
                                            <tr key={p.id}>
                                                <td>
                                                    {p.imageUrl ? (
                                                        <img src={p.imageUrl} alt={p.title} width="100" height="80" />
                                                    ) : (
                                                        "  "
                                                    )}
                                                </td>
                                                <td>{p.name}</td>
                                                <td>{p.description}</td>
                                                <td>
                                                    {p.video ? (
                                                        <a href={p.video} target="_blank" rel="noopener noreferrer">
                                                             
                                                        </a>
                                                    ) : (
                                                        "  "
                                                    )}
                                                </td>
                                                <td>
                                                    {p.previewLink ? (
                                                        <a href={p.previewLink} target="_blank" rel="noopener noreferrer">
                                                             
                                                        </a>
                                                    ) : (
                                                        "  "
                                                    )}
                                                </td>
                                                <td className="d-flex gap-2">
                                                    <NavLink
                                                        to="/control/project/edit"
                                                        state={{ project: p }}
                                                        className="btn btn-success"
                                                    >
                                                        <i className="fa fa-edit"></i>
                                                    </NavLink>
                                                    <Button variant="danger" onClick={() => handleDelete(p.id)}>
                                                        <i className="fa fa-trash"></i>
                                                    </Button>
                                                </td>
                                            </tr>
                                        ))
                                    }
                                </tbody>
                            </Table>
                        </div>
                    </div>
                )}
            </div>
        </>
    )
}

export default IndexProject;