import React, { useState } from "react";
import { getDownloadURL, ref, uploadBytes } from "firebase/storage";
import { Button } from "react-bootstrap";
import { db, storage } from "../../../firebase";
import { addDoc, collection } from "firebase/firestore";
import Swal from "sweetalert2";

const CreateProject = () => {
    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    const [image, setImage] = useState(null);
    const [video, setVideo] = useState(null);
    const [previewLink, setPreviewLink] = useState(null);

    const addProject = async (e) => {
        e.preventDefault();

        try {

            let imageUrl = "";

            if(image){
                const storageRef = ref(storage, `projects/${image.name}`);
                const snapshot = await uploadBytes(storageRef, image);
                imageUrl = await getDownloadURL(snapshot.ref);
            }

            const projectData = {
                name,
                description,
                video,
                imageUrl,
                previewLink
            };

            //   
            await addDoc(collection(db, "projects"), projectData);

            Swal.fire({
                icon: "success",
                title: " ",
                text: "   "
            });

            setName("");
            setDescription("");
            setPreviewLink("");
            setVideo("");
            setImage(null);
        }
        catch(error){
            alert(error);
        }
    }

    return (
        <div className="project_add">
            <h3>  </h3>
            <form onSubmit={addProject}>
                <input 
                    className="form-control mb-2" 
                    type="text" 
                    placeholder=" " 
                    value={name} 
                    onChange={(e) => setName(e.target.value)} 
                />

                <textarea className="form-control mb-2" placeholder=" " cols="3" rows="3" value={description} onChange={(e) => setDescription(e.target.value)}></textarea>


                <input
                    className="form-control mb-2"
                    type="text"
                    placeholder="  ()"
                    value={previewLink}
                    onChange={(e) => setPreviewLink(e.target.value)}
                />

                <input
                    className="form-control mb-2"
                    type="text"
                    placeholder="  ()"
                    value={video}
                    onChange={(e) => setVideo(e.target.value)}
                />

                <label>  </label>
                
                <input
                    className="form-control mb-2"
                    type="file"
                    onChange={(e) => setImage(e.target.files[0])}
                    accept="image/*"
                />

                <Button type="submit" variant="success"> </Button>

            </form>
        </div>
    )
}

export default CreateProject;